"use client"

import { useState, useRef, useEffect } from "react"
import { MessageSquare, X, Send, Bot, User } from "lucide-react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { cn } from "@/lib/utils"
import { companyInfo } from "@/lib/data/company"
import { products } from "@/lib/data/products"

interface Message {
  id: string
  role: "user" | "bot"
  content: string
  timestamp: Date
}

export function Chatbot() {
  const [isOpen, setIsOpen] = useState(false)
  const [messages, setMessages] = useState<Message[]>([
    {
      id: "1",
      role: "bot",
      content:
        "Hello! I am the A V Tech Assistant. How can I help you today? You can ask me about our products, services, or company details.",
      timestamp: new Date(),
    },
  ])
  const [inputValue, setInputValue] = useState("")
  const [isTyping, setIsTyping] = useState(false)
  const scrollRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    if (scrollRef.current) {
      scrollRef.current.scrollTop = scrollRef.current.scrollHeight
    }
  }, [messages, isOpen])

  const handleSendMessage = async () => {
    if (!inputValue.trim()) return

    const userMessage: Message = {
      id: Date.now().toString(),
      role: "user",
      content: inputValue,
      timestamp: new Date(),
    }

    setMessages((prev) => [...prev, userMessage])
    setInputValue("")
    setIsTyping(true)

    // Simulate AI response delay
    setTimeout(() => {
      const botResponse = generateResponse(userMessage.content)
      setMessages((prev) => [
        ...prev,
        {
          id: (Date.now() + 1).toString(),
          role: "bot",
          content: botResponse,
          timestamp: new Date(),
        },
      ])
      setIsTyping(false)
    }, 1000)
  }

  const generateResponse = (query: string): string => {
    const lowerQuery = query.toLowerCase()

    // Products logic
    if (lowerQuery.includes("product") || lowerQuery.includes("panel") || lowerQuery.includes("machine")) {
      const foundProduct = products.find((p) => lowerQuery.includes(p.name.toLowerCase()) || lowerQuery.includes(p.id))
      if (foundProduct) {
        return `${foundProduct.name}: ${foundProduct.shortDescription}. Would you like to know more details?`
      }
      return "We offer a wide range of products including MCC Panels, PCC Panels, PLC Panels, VFD Panels, and Special Purpose Machines. Which one are you interested in?"
    }

    // Services logic
    if (lowerQuery.includes("service") || lowerQuery.includes("support") || lowerQuery.includes("programming")) {
      return "We provide comprehensive services including PLC/Servo Programming, SCADA Systems, Panel Design, and Maintenance Support. How can we assist you?"
    }

    // Contact logic
    if (
      lowerQuery.includes("contact") ||
      lowerQuery.includes("address") ||
      lowerQuery.includes("email") ||
      lowerQuery.includes("phone") ||
      lowerQuery.includes("location")
    ) {
      return `You can reach us at ${companyInfo.contact.phone} or email us at ${companyInfo.contact.emails[0]}. Our office is located in Nashik, Maharashtra.`
    }

    // About logic
    if (lowerQuery.includes("about") || lowerQuery.includes("company") || lowerQuery.includes("who are you")) {
      return "A V Tech is a leading Manufacturer & System Integrator for Factory Automation and Electrical Panels with over 10 years of experience."
    }

    // Default response
    return "I'm not sure I understand. Could you please ask about our products, services, or contact information? You can also chat with us on WhatsApp for immediate assistance."
  }

  return (
    <>
      {/* Chat Toggle Button - Positioned next to WhatsApp button */}
      <button
        onClick={() => setIsOpen(!isOpen)}
        className={cn(
          "fixed bottom-6 right-20 z-50 bg-primary hover:bg-primary/90 text-white p-3 rounded-full shadow-lg transition-all duration-300 hover:scale-105 hover:shadow-xl animate-fade-in",
          isOpen ? "hidden" : "flex",
        )}
        aria-label="Open Chat"
      >
        <MessageSquare className="h-6 w-6" />
      </button>

      {/* Chat Window */}
      <div
        className={cn(
          "fixed bottom-24 right-6 z-50 w-80 md:w-96 bg-white rounded-2xl shadow-2xl border border-gray-200 overflow-hidden transition-all duration-300 origin-bottom-right flex flex-col",
          isOpen ? "scale-100 opacity-100" : "scale-0 opacity-0 pointer-events-none",
        )}
        style={{ maxHeight: "500px", height: "80vh" }}
      >
        {/* Header */}
        <div className="bg-primary p-4 flex justify-between items-center text-white">
          <div className="flex items-center gap-2">
            <div className="bg-white/20 p-1.5 rounded-full">
              <Bot className="h-5 w-5" />
            </div>
            <div>
              <h3 className="font-bold text-sm">A V Tech Assistant</h3>
              <p className="text-xs text-white/80 flex items-center gap-1">
                <span className="w-2 h-2 bg-green-400 rounded-full animate-pulse"></span>
                Online
              </p>
            </div>
          </div>
          <button onClick={() => setIsOpen(false)} className="text-white/80 hover:text-white transition-colors">
            <X className="h-5 w-5" />
          </button>
        </div>

        {/* Messages Area */}
        <div className="flex-1 overflow-y-auto p-4 bg-gray-50" ref={scrollRef}>
          <div className="space-y-4">
            {messages.map((msg) => (
              <div
                key={msg.id}
                className={cn("flex gap-2 max-w-[85%]", msg.role === "user" ? "ml-auto flex-row-reverse" : "")}
              >
                <div
                  className={cn(
                    "w-8 h-8 rounded-full flex items-center justify-center shrink-0",
                    msg.role === "user" ? "bg-gray-200" : "bg-primary/10 text-primary",
                  )}
                >
                  {msg.role === "user" ? <User className="h-4 w-4 text-gray-600" /> : <Bot className="h-4 w-4" />}
                </div>
                <div
                  className={cn(
                    "p-3 rounded-2xl text-sm shadow-sm",
                    msg.role === "user"
                      ? "bg-primary text-white rounded-tr-none"
                      : "bg-white text-gray-700 border border-gray-100 rounded-tl-none",
                  )}
                >
                  {msg.content}
                  <div
                    className={cn(
                      "text-[10px] mt-1 opacity-70",
                      msg.role === "user" ? "text-white/80" : "text-gray-400",
                    )}
                  >
                    {msg.timestamp.toLocaleTimeString([], { hour: "2-digit", minute: "2-digit" })}
                  </div>
                </div>
              </div>
            ))}
            {isTyping && (
              <div className="flex gap-2 max-w-[85%]">
                <div className="w-8 h-8 rounded-full bg-primary/10 text-primary flex items-center justify-center shrink-0">
                  <Bot className="h-4 w-4" />
                </div>
                <div className="bg-white p-3 rounded-2xl rounded-tl-none border border-gray-100 shadow-sm flex items-center gap-1">
                  <span
                    className="w-2 h-2 bg-gray-400 rounded-full animate-bounce"
                    style={{ animationDelay: "0ms" }}
                  ></span>
                  <span
                    className="w-2 h-2 bg-gray-400 rounded-full animate-bounce"
                    style={{ animationDelay: "150ms" }}
                  ></span>
                  <span
                    className="w-2 h-2 bg-gray-400 rounded-full animate-bounce"
                    style={{ animationDelay: "300ms" }}
                  ></span>
                </div>
              </div>
            )}
          </div>
        </div>

        {/* Input Area */}
        <div className="p-3 bg-white border-t border-gray-100">
          <form
            onSubmit={(e) => {
              e.preventDefault()
              handleSendMessage()
            }}
            className="flex gap-2"
          >
            <Input
              value={inputValue}
              onChange={(e) => setInputValue(e.target.value)}
              placeholder="Type your message..."
              className="flex-1 focus-visible:ring-primary"
            />
            <Button type="submit" size="icon" className="bg-primary hover:bg-primary/90 shrink-0">
              <Send className="h-4 w-4" />
            </Button>
          </form>
        </div>
      </div>
    </>
  )
}
